import React, { Suspense } from 'react';
import { render } from 'react-dom';
import './i18n';
import App from './containers/App';
import GlobalState from './containers/GlobalState';

render(
    <Suspense fallback='loading'>
        <GlobalState>
            <App />
        </GlobalState>
    </Suspense>,
    document.getElementById('root')
);
